import http from '@/components/utils/http.js'
import {getShoplist} from '@/api/supervisor/manager.js'
export default {

	data: function() {
		return {
		 
			pageLoad: false,
			pageData: [],
			catid:0,
			height:440,
			sideTop:0
		}
	},
	onLoad() {
		if (this.$store.state.userId == '') {
			uni.showModal({
				title: '提示',
				content: '请在微信授权后查看！',
				showCancel: true,
				success: function(rest) {
					if (rest.confirm) {
						uni.navigateTo({
							url: '/pages/login/login'
						});
					} else {
						uni.navigateBack({
						    delta: 1,
						    animationDuration: 200
						});
					}
				}
			});
		} else {
		var win=uni.getSystemInfoSync();
		this.height=win.windowHeight-50;

		this.sideTop=0;

		this.getPage();
		}
	},
	methods: {
		setCat:function(catid){
			this.catid=catid;
		},
		goList:function(catid){
			// uni.navigateTo({
			// 	url:"?catid="+catid
			// })
		},
		getPage() {
			var that = this;
			getShoplist().then(res=>{
				if(res.data.code==200)
				{
					that.pageData = res.data.data;
					console.log(that.pageData);
					for(var i in res.data.data){
							that.catid=res.data.data[i].shopUuid;
							break;
						}
						that.pageLoad = true;
				}else{
					uni.showModal({
						title: '提示',
						content: res.data.message,
						showCancel: false
					})
				}
			})
			
		}
	}
}
